
body {
  background-image: url("./assets/background.jpg");
  background-attachment: fixed;
  margin: 0;
  padding: 0;
}
h1, h2 {
  color: #fff;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  overflow: hidden;
  width: 100%;
}
#sidebar {
    background-color: #fff;
    box-shadow: 4px 0px 2px #fff;
    float:left;
    width:20vmax;
    height: 100vmin;
    text-align: center;
    position: fixed;
}
.menu-img {
  height: 9vmin;
}
#links {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.link{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-item-align: center;
      align-self: center;
}
#links a {
  color: #000;
  text-decoration: none;
}
.router-link-active {
  color: #0D4A5C !important;
  border-bottom: 2px solid #0D4A5C;
}
#content {
  margin: 0 0 0 15vmax;
}
#main-logo {
  height: 13vmax;
  width:  20vmax;
}
footer {
  background-color: #fff;
  bottom: 0;
  color: #000;
  position: relative;
}
.or-spacer {
  width: 20vmax;
  position: relative;
}
.or-spacer .mask {
  overflow: hidden;
  height: 20px;
}
.or-spacer .mask:after {
  content: '';
  display: block;
  margin: -25px auto 0;
  width: 100%;
  height: 25px;
  border-radius: 125px / 12px;
  box-shadow: 0 0 8px black;
}
.fa {
  color:  #000;
  padding: 4px;
}




